import * as React from "react";
import { Box, Container } from "@mui/material";
import { styled } from "@mui/material/styles";
import Welcome from "./sections/Welcome";
import Services from "../../components/Services";
import PointsOfInterest from "./sections/PointsOfInterest";
import Map from "./sections/Map";

const WelcomeSection = styled(Box)(({ theme }) => ({
  width: "100%",
  backgroundColor: theme.palette.white.main,
  color: theme.text.white.main
}));

const ServicesSection = styled(Box)(({ theme }) => ({
  width: "100%",
  backgroundColor: theme.palette.white.main,
  marginTop: "2rem",
  color: theme.text.white.muted
}));

const PointsOfInterestSection = styled(Box)(({ theme }) => ({
  width: "100%",
  backgroundColor: theme.palette.white.main,
  marginTop: "2rem",
  color: theme.text.white.main
}));

const MapSection = styled(Box)(({ theme }) => ({
  marginTop: "2rem",
  width: "100%",
  height: "25vh",
  minHeight: "175px",
  position: "relative",
  "& .leaflet-control-attribution > a > svg": {
    visibility: "hidden"
  }
}));

export default function Home() {
  return (
    <Container maxWidth={false}>
      <WelcomeSection component="section" id="welcome">
        <Welcome />
      </WelcomeSection>
      <ServicesSection component="section" id="services">
        <Services />
      </ServicesSection>
      <PointsOfInterestSection component="section" id="values">
        <PointsOfInterest />
      </PointsOfInterestSection>
      <MapSection component="section" id="map">
        <Map />
      </MapSection>
    </Container>
  );
}
