import * as React from "react";
import { Box } from "@mui/material";
import { styled } from "@mui/material/styles";
import NavBar from "../../../components/Header/Navbar";
import ContactUs from "../../../components/Buttons/ContactUs";

const Wrapper = styled(Box)(({ theme }) => ({
  background:
    `linear-gradient(to top, rgba(255,255,255,0.075), rgba(255,255,255,0.075), rgba(255,255,255,0.375)),` +
    `linear-gradient(to left, rgba(255,255,255,0.375), rgba(255,255,255,0.075), rgba(255,255,255,0.375)),` +
    `url('/images/welcome-bg.jpg')`,
  backgroundRepeat: "no-repeat",
  backgroundSize: "cover",
  backgroundPosition: "center",
  borderRadius: "1rem",
  height: "calc(100vh - 4rem)",
  minHeight: "400px"
}));

export default function Welcome() {
  return (
    <Wrapper>
      <NavBar />
      <Box sx={{ position: "absolute", top: "75%", left: "15%" }}>
        <ContactUs />
      </Box>
    </Wrapper>
  );
}
